}
/* Content */
.content {
    min-height: 250px;
    padding: 15px 0;
    margin-right: auto;
    margin-left: auto;}
/* H1 - H6 font */
h1,
h2,
h3,
h4,
h5,
h6,
.h1,
.h2,
.h3,
.h4,
.h5,
