.bool-slider.true .inset {
  background-color: #00a4b6;
  *background-color: #475264;
  background-image: -webkit-gradient(linear, left top, left bottom, from(#62c462), to(#51a351));
  background-image: -webkit-linear-gradient(top, #62c462, #51a351);
  background-image: linear-gradient(top, #62c462, #51a351);
  background-repeat: repeat-x;
  border-color: #51a351 #51a351 #387038;
  border-color: rgba(0, 0, 0, 0.1) rgba(0, 0, 0, 0.1) rgba(0, 0, 0, 0.25);
  background-image: none;
  /*box-shadow: inset 0 2px 4px rgba(0, 0, 0, 0.15), 0 1px 2px rgba(0, 0, 0, 0.05);*/ }

.bool-slider.true .inset .control {
  float: left; }

.bool-slider.true .inset .control:after {
  content: 'Rejected Claims';
  position: absolute;
  top: 20%;
  left: 24%; }

.bool-slider.false .inset {
  background-color: #00a4b6;
  *background-color: #475264;
  background-image: -webkit-gradient(linear, left top, left bottom, from(#ee5f5b), to(#bd362f));
  background-image: -webkit-linear-gradient(top, #ee5f5b, #bd362f);
  background-image: linear-gradient(top, #ee5f5b, #bd362f);
  background-repeat: repeat-x;
  border-color: #bd362f #bd362f #802420;
  border-color: rgba(0, 0, 0, 0.1) rgba(0, 0, 0, 0.1) rgba(0, 0, 0, 0.25);
  background-image: none;
  /*box-shadow: inset 0 2px 4px rgba(0, 0, 0, 0.15), 0 1px 2px rgba(0, 0, 0, 0.05);*/ }

.bool-slider.false .inset .control {
  float: right; }

.bool-slider.false .inset .control:before {
  content: 'Approved Claims';
  position: absolute;
  right: 23%;
  top: 20%; }

.bool-slider .inset {
  height: 100%;
  border-radius: 20px; }

.bool-slider .inset .control {
  background-color: #000;
  width: 25px;
  height: 90%;
  border-radius: 20px;
